// app/api/gas2/route.ts
import { NextRequest, NextResponse } from 'next/server';

const GAS_BASE =
  process.env.GAS_BASE ||
  process.env.NEXT_PUBLIC_GAS_BASE ||
  '';

const GAS_TOKEN = process.env.GAS_TOKEN || process.env.NEXT_PUBLIC_GAS_TOKEN || ''; // optional

const TIMEOUT_MS = 15000;

function withQuery(url: string, qs: Record<string, string | number | undefined>) {
  const u = new URL(url);
  for (const [k, v] of Object.entries(qs)) {
    if (v !== undefined && v !== null && String(v) !== '') {
      u.searchParams.set(k, String(v));
    }
  }
  return u.toString();
}

async function forward(method: 'GET' | 'POST', url: string, body?: any) {
  if (!GAS_BASE) {
    return NextResponse.json({ ok: false, error: 'Missing NEXT_PUBLIC_GAS_BASE' }, { status: 500 });
  }

  const ac = new AbortController();
  const t = setTimeout(() => ac.abort(), TIMEOUT_MS);

  try {
    const headers: Record<string, string> = { 'Content-Type': 'application/json' };

    // If GAS_TOKEN is set, ensure GAS gets it the way your script expects:
    //  - For GET: as ?token=...
    //  - For POST: inside JSON body as { token: "..." }
    const gasUrl = new URL(url);
    if (GAS_TOKEN && !gasUrl.searchParams.get('token')) {
      gasUrl.searchParams.set('token', GAS_TOKEN);
    }

    const postBody =
      method === 'POST'
        ? JSON.stringify({
            ...(body ?? {}),
            ...(GAS_TOKEN ? { token: GAS_TOKEN } : {}),
          })
        : undefined;

    const res = await fetch(gasUrl.toString(), {
      method,
      headers,
      body: postBody,
      signal: ac.signal,
      cache: 'no-store',
      next: { revalidate: 0 },
    });

    const ct = res.headers.get('content-type') || '';
    if (!ct.includes('application/json')) {
      const text = await res.text();
      return NextResponse.json({ ok: false, error: 'Non-JSON from GAS', body: text }, { status: 502 });
    }

    const json = await res.json();
    return NextResponse.json(json, { status: res.status });
  } catch (err: any) {
    const msg = err?.name === 'AbortError' ? 'Timeout contacting GAS' : (err?.message || 'Fetch error');
    return NextResponse.json({ ok: false, error: msg }, { status: 504 });
  } finally {
    clearTimeout(t);
  }
}

export async function GET(req: NextRequest) {
  const urlObj = new URL(req.url);
  const action = urlObj.searchParams.get('action') || urlObj.searchParams.get('endpoint') || '';
  const tag = urlObj.searchParams.get('tag') || '';
  const q = urlObj.searchParams.get('q') || '';

  const url = withQuery(GAS_BASE, {
    action: action || (q ? 'search' : tag ? 'get' : 'ping'),
    tag: tag || undefined,
    q: q || undefined,
    // token appended in forward()
  });

  return forward('GET', url);
}

export async function POST(req: NextRequest) {
  const body = await req.json().catch(() => ({}));
  const action =
    body?.action ||
    body?.endpoint ||
    (body?.job ? 'save' : body?.tag && body?.reason ? 'log-call' : body?.tag ? 'progress' : 'ping');

  const url = withQuery(GAS_BASE, { action /* token appended in forward() */ });
  return forward('POST', url, body);
}
